
import { defineComponent, reactive, toRefs } from 'vue';

export default defineComponent({
    setup(){
        let messageList = [
            '很快啊，就又过年了',
            '希望大小姐健健康康',
            '快快乐乐',
            '咩都翻晒来'
        ];
        let state = reactive({
            currentMsg:messageList[0]
        });
        return {
            ...toRefs(state)
        }
    }
})
